body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  z-index: 2;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-sec;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  z-index: 1;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-min;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation: 10s linear infinite rotate-sec;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation: 600s steps(60, end) infinite rotate-min;
}

@keyframes rotate-sec {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

@keyframes rotate-min {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}
